
import { defineComponent } from 'vue';
import ManageUserModal from '@/components/AfterLogin/parts/ManageUser/ManageUserModal.vue';
import XLSX from 'xlsx';
import { saveAs } from 'file-saver';

export default defineComponent({
	name: 'ManageUserView',
    data() {
        return {
            test:''
        }
    },
	components: {
		ManageUserModal
	},
    methods:{
        DlFormat(){
            var array1 =
            [
                ["名前", "メールアドレス"]
            ];

            var write_opts = {
                type: 'binary'
            };
            var wb = this.aoa_to_workbook(array1, write_opts);
            if(!XLSX){
                return;
            }

            const wopts: XLSX.WritingOptions = {
                bookType: "xlsx",
                bookSST: true,
                type: "array"
            };

            var wb_out = XLSX.write(wb, wopts);
            var blob = new Blob([this.s2ab(wb_out)], { type: 'application/octet-stream' });
            saveAs(blob, 'myExcelFile.xlsx');
        },

        aoa_to_workbook (data:any, opts:any) {
            return this.sheet_to_workbook(XLSX.utils.aoa_to_sheet(data), opts);
        },

        sheet_to_workbook(sheet:any, opts:any){
            var n = opts && opts.sheet ? opts.sheet : "Sheet1";
            var sheets:any = {}; sheets[n] = sheet;
            return { SheetNames: [n], Sheets: sheets };
        },

        s2ab(s:any){
            var buf = new ArrayBuffer(s.length);
            var view = new Uint8Array(buf);
            for (var i = 0; i != s.length; ++i) view[i] = s.charCodeAt(i) & 0xFF;
            return buf;
        }
    },
    /*mounted () {
        console.log(this.$refs['modal'].$el.querySelector('#csvmodal'))
    }*/
});
